<template>
    <div>
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"
                >首页</el-breadcrumb-item
            >
            <el-breadcrumb-item>订单管理</el-breadcrumb-item>
            <el-breadcrumb-item>订单列表</el-breadcrumb-item>
        </el-breadcrumb>
        <el-card>
            <el-row>
                <el-col :span="8">
                    <el-input
                        placeholder="请输入订单号"
                        v-model="queryParams.query"
                        class="input-with-select"
                        clearable
                        @clear="clearQueryInput"
                    >
                        <el-button
                            slot="append"
                            icon="el-icon-search"
                        ></el-button>
                    </el-input>
                </el-col>
            </el-row>
            <el-table :data="orderList" stripe border>
                <el-table-column
                    label="#"
                    type="index"
                    align="center"
                ></el-table-column>
                <el-table-column
                    label="订单编号"
                    prop="order_number"
                ></el-table-column>
                <el-table-column
                    label="订单价格"
                    prop="order_price"
                    align="center"
                ></el-table-column>
                <el-table-column label="是否支付" align="center">
                    <template v-slot="data">
                        <el-tag v-if="data.row.pay_status === '0'" type="danger"
                            >未支付</el-tag
                        >
                        <el-tag v-else type="success">已支付</el-tag>
                    </template>
                </el-table-column>
                <el-table-column
                    label="是否发货"
                    prop="is_send"
                    align="center"
                ></el-table-column>
                <el-table-column
                    label="下单时间"
                    prop="create_time"
                    align="center"
                ></el-table-column>
            </el-table>
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="parseInt(currentPage)"
                :page-sizes="[1, 2, 5, 7, 10, 15, 50]"
                :page-size="queryParams.pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total"
            >
            </el-pagination>
        </el-card>
    </div>
</template>

<script>
import dayjs from "dayjs";
export default {
    name: "Orders",
    created() {
        this.getOrderList();
    },
    data() {
        return {
            // 查询参数
            queryParams: {
                query: "",
                pagenum: 1,
                pagesize: 7,
            },
            // 订单列表
            orderList: [],
            // 总页数
            total: 0,
            // 当前页码
            currentPage: 1,
        };
    },
    methods: {
        async getOrderList() {
            const { data: res } = await this.$http.get(`orders`, {
                params: this.queryParams,
            });
            if (res.meta.status !== 200)
                return this.$message({
                    message: res.meta.msg,
                    type: "error",
                    center: true,
                });

            res.data.goods.forEach((item) => {
                item.create_time = dayjs(item.create_time * 1000).format(
                    "YYYY-MM-DD HH:mm:ss"
                );
                item.update_time = dayjs(item.update_time * 1000).format(
                    "YYYY-MM-DD HH:mm:ss"
                );
            });

            this.orderList = res.data.goods;
            this.total = res.data.total;
            this.currentPage = res.data.pagenum;
        },
        clearQueryInput() {
            this.getOrderList();
        },
        handleSizeChange(newPageSize) {
            this.queryParams.pagesize = newPageSize;
            this.getOrderList();
        },
        handleCurrentChange(newPageNum) {
            this.queryParams.pagenum = newPageNum;
            this.getOrderList();
        },
    },
};
</script>

<style lang="less" scoped>
.el-pagination {
    text-align: left;
}
</style>